.App {
  position: absolute;
  width: 192rem;
  font-family: SY-Normal;
}

h1 {
  font-weight: 500;
  font-size: 7rem;
}

h2 {
  font-weight: 500;
  font-size: 3.6rem;
}

h3 {
  font-weight: 500;
  font-size: 2rem;
}

h4 {
  font-weight: 500;
  font-size: 1.8rem;
}

h5 {
  font-weight: 500;
  font-size: 1.6rem;
}


/* 菜单的高度 */
.ant-menu-horizontal {
  line-height: 8rem;
}

/* 选中菜单状态 */
.ant-menu-item-selected a {
  color: #c9b17a !important;
  font-family: 微软雅黑;
  font-weight: bold;
}

.ant-menu-submenu-selected {
  color: #c9b17a !important;
  font-family: 微软雅黑;
  font-weight: bold;
}

/* 选择的菜单下划线的颜色 */
.ant-menu-submenu-selected::after,
.ant-menu-item-selected::after {
  border-bottom-color: #c9b17a !important;
  left: 0 !important;
  right: 0 !important;
  width: 100% !important;
}